import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/components/page/index';
import EditView from '@/components/page/editView';
// import Login from '@/components/login';

Vue.use(Router);

export default new Router({
  // mode: 'hash', // history hash
  routes: [
    {path: '/index', name: 'index', component: Index, meta: {requiresAuth: false}},
    // {path: '/login', name: 'login', component: Login},
    {path: '/addview', name: 'addview', component: EditView, meta: {requiresAuth: false}},
    {path: '/editview/:id', name: 'editview', component: EditView, meta: {requiresAuth: false}},
    {path: '*', redirect: '/index'}
  ]
});


