<template>
  <div class="puzzle-wrapper iframe">
    <div class="config">
      <div class="form">
        <p>
          <o-input label="URL" placeholder="" v-model="url" @click="op.url = url" @blur="op.url = url"></o-input>
        </p>
      </div>
    </div>
    <div class="preview">
      <div class="content">
        <o-iframe class="components" :option="op"></o-iframe>
      </div>
    </div>
  </div>
</template>

<script>
  import oIframe from '@/components/ui/iframe';

  export default {
    components: {
      oIframe
    },
    props: {
      option: {
        type: Object,
        default() {
          return {
            component: 'oIframe',
            url: ''
          };
        }
      }
    },
    data() {
      return {
        url: '',
        op: {
          component: 'oIframe',
          url: ''
        }
      };
    },
    watch: {
      op: {
        deep: true,
        handler() {
          this.$emit('update', this.op);
        }
      }
    },
    methods: {},
    mounted() {
      this.op.url = this.url = this.option.url || '';
      this.$forceUpdate();
    }
  };
</script>

<style lang="scss">
  .puzzle-wrapper.iframe {
    .config {
      margin: 0;
      border: 0px;
    }
    .preview .content {
      width: 90%;
    }
  }
</style>

