var auth = {
  name: '',
  img: '',
  sex: '',
  sexuality: '',
  phone: '',
  constellation: '',
  zodiac: '',
  birthday: '',
  uid: '',
  token: ''
};

const relic = {
  setUser(ob) {
    auth.name = ob.name;
    auth.img = ob.img;
    auth.sex = ob.sex || 0;
    auth.sexuality = ob.sexuality;
    auth.phone = ob.phone;
    auth.constellation = ob.constellation;
    auth.zodiac = ob.zodiac;
    auth.birthday = ob.birthday;
    auth.uid = ob.login_id;
    auth.token = ob.token;
    localStorage.ELPSYCONGROOLAYOUT = window.btoa(window.encodeURIComponent(JSON.stringify(ob))).split('T').map(function(i){ return i.split('').reverse().join('');}).join('T');
  },
  clear() {
    localStorage.removeItem('ELPSYCONGROOLAYOUT');
    auth.name = '';
    auth.img = '';
    auth.sex = '';
    auth.sexuality = '';
    auth.phone = '';
    auth.constellation = '';
    auth.zodiac = '';
    auth.birthday = '';
    auth.uid = '';
    auth.token = '';
  },
  get isLogin() {
    if(auth.name && auth.token) {
      return true;
    }else {
      return false;
    }
  },
  get uid() {return auth.uid; },
  set uid(id) {
    auth.uid = id;
    localStorage.ELPSYCONGROOID = window.btoa(window.encodeURIComponent(JSON.stringify(id))).split('T').map(function(i){ return i.split('').reverse().join('');}).join('T');
  },
  get token() {return auth.token; }
};

if(localStorage.ELPSYCONGROOLAYOUT) {
  var cache = JSON.parse(window.decodeURIComponent(window.atob(localStorage.ELPSYCONGROOLAYOUT.split('T').map(function(i){ return i.split('').reverse().join('');}).join('T'))));
  relic.setUser(cache);
}
if(localStorage.ELPSYCONGROOID) {
  relic.uid = JSON.parse(window.decodeURIComponent(window.atob(localStorage.ELPSYCONGROOID.split('T').map(function(i){ return i.split('').reverse().join('');}).join('T'))));
}

export default relic;


