<template>
  <div class="box-iframe">
    <iframe :src="url" frameborder="0" scrolling="no"></iframe>
  </div>
</template>

<script>
  export default {
    props: {
      option: {
        type: Object,
        default() {
          return {};
        }
      }
    },
    computed: {
      url() {
        return this.option.url || '';
      }
    }
  };
</script>

<style lang="scss">
  .box-iframe {
    background: #fff !important;
    iframe {
      width: 100%;
      height: 100%;
    }
  }
</style>


